@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap);

header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 100px ;
    background-color:white ;
    position: relative;
    z-index: 3;
}
header::before{
    content: '';
    position: absolute;
    top: 100px;
    width: calc(100% - 200px);
    height: 1px;
    background-color: hsl(219, 9%, 45%);
    opacity: 30%;
    
}
.nav-close-button , .menu-button{
    display: none;
}
.logo-nav , .cart-profile{
    display: flex;
    align-items: center;
}
nav{
    margin-left: 20px;
}
nav > ul {
    display: flex;
}
nav > ul > li {
    margin: 0 15px;
    
}
nav > ul > li > a{
    color: hsl(219, 9%, 45%);
}
