    background-color: hsl(26, 100%, 55%);
    
} */
.cart{
    margin-right: 30px; 
    cursor: pointer;   
}
.profile-picture{
    width: 50px;
    cursor: pointer;   
}

@media screen and (max-width: 800px) {

    header{
        padding: 20px 5% ;
        position: fixed;
        width: 100vw;
    }
    header::before{
        display: none;
    }
    .menu-button{
        all: unset;
        display: flex;
        align-items:center ;
        justify-content: center;
        margin-right: 15px;
       
    }
    .nav-close-button{
        all: unset;
        position: absolute;
        top: 0rem;
        left: 0rem;
        margin-top: 20px;
        margin-left: 3rem;
        padding: 15px;
    }
    .menu-button svg , .nav-close-button svg {
        -webkit-transform: scale(120%);
                transform: scale(120%);
    }

    .logo-nav , .cart-profile{
        display: flex;
        align-items: center;
    }
    nav{
        margin-left: 20px;
        z-index: 3;
    }
    nav::before{
        content: '';
        position: fixed;
        background-color: rgba(0, 0, 0, 0.502);
        top: 0;
        right: 0;
        width: 40%;
        height: 100vh;
    }
    nav > ul {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 0rem;
        left: 0rem;
        width: 60%;
        height: 100vh;
        background-color: white;
        z-index: -1;
        padding-top: 6rem;
        padding-left: 3rem;
    }
    nav > ul > li {
        margin: 15px;  
    }
    nav > ul > li > a{
        color: black;
        font-weight: 700;
    }
    .cart{
        margin-right: 30px; 
        cursor: pointer;   
    }
    .profile-picture{
        width: 50px;
        cursor: pointer;   
    }

}
.photo-gallery{
    width: 400px;
}
.product{
    width: 400px;
    border-radius: 20px;
}
.products-images{
    display: flex;
    margin-top: 40PX;
    justify-content: space-between;
}
.products-images > img {
    width: 20%;
    border-radius: 10px;
}
.products-images> img:hover {
    cursor: pointer;
    -webkit-filter: opacity(50%);
            filter: opacity(50%);
}


.active-image{
    cursor: pointer;
    -webkit-filter: opacity(50%);
            filter: opacity(50%);
    border: 3px hsl(26, 100%, 55%) solid;
}

@media screen and (max-width: 800px) {
    .photo-gallery{
        width: 100%;
    }
    .product{
        width: 100%;
        border-radius: 0px;
    }
    .products-images{
        display: none;
    }
    
}
.Product-section{
    display: flex;
    padding: 20px 150px ;
    align-items: center;
    justify-content: center;
    margin-top: 80px;
}
.product-photos{
    margin-right: 40px;
}
.product-details{
    margin-left: 40px;
}
.company-name{
    font-weight: 700;
    color: hsl(26, 100%, 55%);
    opacity: 70%;
    font-size: 16px;
}

.product-title{
    display: block;
    font-size: 40px;
    margin: 20px 0;
    max-width:400px;
}
.product-description {
    display: flex;
    max-width: 44ch;
    margin: 10px 0;
    opacity: 50%;
    font-size: 14px;
}
.price-details{

    display: flex;
    align-items: center;
    margin: 15px 0;
}
.price{
    font-weight: 700;
    font-size: 25px;
}
.discount{
    background-color: hsl(26, 97%, 88%) ;
    color: hsl(26, 100%, 55%) ;
    padding: 2px 6px;
    font-size: 13px;
    border-radius: 3px;
    margin-left: 10px;
}
.old-price{
    font-size: 13px;
    font-weight: 700;
    opacity: 30%;
    text-decoration:line-through;
}

.purchase-detail{
    display: flex;
    align-items: center;
    margin: 20px 0;
}
.quantity-details{
    width: 40%;
    margin-right: 20px;
    padding: 10px 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: hsl(0, 0%, 95%);
}
.quantity-details > button {
    all: unset;
    cursor: pointer;
    color:  hsl(26, 100%, 55%);
    font-size: 20px;
    font-weight: 700;
}
.quantity{
    all: unset;
    text-align: center;
    width: 50px;
    height: 20px;
}
.cart-button{
    all: unset;
    width: 75%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: hsl(26, 100%, 55%) ;
    padding: 10px 0px;
    border-radius: 10px;
    color: white;
    font-weight: 700;
    cursor: pointer;
}
.cart-icon{
    margin-right: 5px;
}
.photoGallery-status{
    display: flex;
}
.photoGallery-mobile-status{
    display: none;
}
