.photo-gallery-mobile{
    position: relative;
}
.product-image-mobile{
    width: 100%;
}
.close-button{
    all: unset;
    position: absolute;
    right: 0;
    top: -30px;
    -webkit-transform: scale(180%);
            transform: scale(180%);
    color: orange;
    cursor: pointer;

}
.close-button path {
    fill: hsl(220, 13%, 13%);
}
.close-button:hover path {
    fill: hsl(26, 100%, 55%);
}

.icons-button-mobile{
    all: unset;
    width: 25px;
    height: 25px;
    background-color: white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    position: absolute;
    top: 50%;
    cursor: pointer;
}
.icon{
   -webkit-transform: scale(100%);
           transform: scale(100%);
}
.previous-icon-mobile{
    left: 15px;
}
.next-icon-moblie{
    right:  15px;
}

.cart-section{
    position: absolute;
    top: 5rem;
    right: 3rem;
    width: 400px;
    min-height: 250px;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    box-shadow:
    0px 4.4px 11.7px rgba(0, 0, 0, 0.066),
    0px 55px 171px rgba(0, 0, 0, 0.08)
  ;
  z-index: 10;
  
}
.cart-name{
    font-weight: 700;
    padding: 20px ;
    border-bottom: 1px solid rgb(190, 190, 190);
}
.no-items{
  text-align: center;
  margin-top: 75px;
  font-weight: 700;
  opacity: 70%;
}

@media screen and (max-width: 800px) {
  .cart-section{
    position: fixed;
    top: 6rem;
    right: 5%;
    width: 90%;
  }

}

.cart-items {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 15px;

}
.cart-items > img{
    width: 50px;
    border-radius: 5px;
}
.item-details {

    color: rgb(188, 188, 188);
}
.item-details > h3 {
    font-weight: 400;
    padding-bottom: 5px;
}
span {
    margin-left: 5px;
    color: black;
    font-weight: 700;
}
.cart-items > button {
    all: unset;
    
}
button > svg {
    -webkit-transform: scale(60%);
            transform: scale(60%);
    cursor: pointer;
}
.checkout-button{
    all: unset;
    text-align: center;
    font-size: 14px;
    font-weight: 700;
    color: white;
    background-color:  hsl(26, 100%, 55%);
    width: 80%;
    padding: 17px;
    border-radius: 10px;
    margin-left:6%;
    margin: 2rem 6%;
    cursor: pointer;
}

@media screen and (max-width: 800px) {
    .item-details > h3 {
        font-weight: 400;
        padding-bottom: 5px;
        font-size: 14px;
    }
}
